body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.table__cell {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
}
.table__cell--centered {
    -webkit-justify-content: center;
            justify-content: center;
}

.table__cell-btn {
    margin: 0 10px;
}

.ReactTable .rt-thead .rt-th {
  border: none;
}

.ReactTable .rt-tbody .rt-td {
  border: none;
}

.ReactTable .rt-tbody .rt-tr-group {
  border: none;
  background-color: #F1F8FF;
  margin: 2px;
}

.ReactTable .rt-thead.-header {
  box-shadow: none;
  border-bottom: 2px solid rgba(215, 215, 219, 0.5);
  font-size: 18px;
  font-weight: 600;
  color: #737373
}

.ReactTable {
  border: none;
  padding: 50px;
  height: 400px;
}

.ReactTable .rt-tbody .rt-tr-group:hover {
  background-color: #0179FF;
  color: white;
  font-weight: 600;
}

.ReactTable .rt-thead .rt-th.-sort-desc {
  background-color: rgba(12,12,13,0.1);
  box-shadow: none;
}
.ReactTable .rt-thead .rt-th.-sort-asc {
  background-color: rgba(12,12,13,0.1);
  box-shadow: none;
}

.ReactTable .rt-td.status-cell {
  padding: 0;
  box-shadow: 4px 0px 0px 0px rgba(255,255,255,1);
}

.no-data-tbody {
  height: 300px;
  font-weight: 600;
  font-size: 28px;
  color: rgba(215,215,219,0.5);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.rt-th.-sort-asc .sort-icons, .rt-th.-sort-desc .sort-icons {
  display: -webkit-flex;
  display: flex;
}

.-sort-asc .sort-up {
  color:red;
}
.-sort-desc .sort-down {
  color:red;
}
